import Vue from 'vue'
import Router from 'vue-router'
import Intro from '@/pages/Intro'
import blogHome from '@/pages/blogHome'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Intro',
      component: Intro
    },
    {
      path: '/blogHome',
      name: 'blogHome',
      component: blogHome
    }
  ]
})



