<template>
  <el-container>
    <el-header>Header</el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu background-color="#417EB7" text-color="#fff">
      <el-submenu index="1">
        <template slot="title">
          <span>Front-end</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="1-1" @click="showthis()">HTML</el-menu-item>
          <el-menu-item index="1-2">CSS</el-menu-item>
          <el-menu-item index="1-3">JavaScript</el-menu-item>
          <el-menu-item index="1-4">Framework</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-menu-item index="2">
        <span slot="title">Computer</span>
      </el-menu-item>
      <el-menu-item index="3">
        <span slot="title">Others</span>
      </el-menu-item>
    </el-menu>
    </el-aside>
      <el-container>
      <el-main>
        <articleBar v-for="item in showList" :name="item.name" :url="item.url" :descript="item.descript" :key="item.name"></articleBar>
      </el-main>
      <el-footer>Footer</el-footer>
      </el-container>
    </el-container>
</el-container>
</template>

<script>
import articleList from "@/assets/article.json";
import articleBar from "@/components/articleBar";
export default {
  name: "blogHome",
  data() {
    return {
      showList: []
    };
  },
  mounted() {
    this.showList = JSON.parse(JSON.stringify(articleList));
  },
  components: {
    articleBar
  },
  methods: {}
};
</script>


<style scoped>
.el-header {
  background-color: #417eb7;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-footer {
  background-color: #1297d7;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-aside {
  text-align: center;
  color: white;
  border: 1px solid #ccc;
  background: -webkit-linear-gradient(
    bottom,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(65, 126, 183) 100%
  );
  /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    top,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(65, 126, 183) 100%
  );
  /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    top,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(65, 126, 183) 100%
  );
  /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to top,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(65, 126, 183) 100%
  );
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: left;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>



