<template>
    <section class="box" @click="gotoPage()">
        <h2>{{name}}</h2>
        <h4>{{descript}}</h4>
    </section>
</template>

<script>
import axios from "axios";
import marked from "marked";
export default {
  name: "articleBar",
  data() {
    return {};
  },
  props: ["name", "descript", "url"],
  mounted() {},
  methods: {
    gotoPage() {
      let url = this.url;
      console.log(marked);
      axios
        .post("../article/" + url, {})
        .then(function(data) {
          let text = marked(data);
          console.log(text);
        });
    }
  }
};
</script>


<style scoped>
.box {
  width: 90%;
  margin-top: 10px;
  border: 1px solid #333;
  border-left: 2px solid green;
  padding-left: 20px;
  cursor: pointer;
}
</style>


